<template>
  <div class="hello">
    <div id='surveyElement'>
      <survey :survey="surveyModel"/> <!-- surveyJS  surveyModel  -->
    </div>
  </div>
</template>

<script>
import * as SurveyVue from 'survey-vue' // surveyJS import
import surveyJSON from '@/assets/survey.json' //  JSON  

let Survey = SurveyVue.Survey // surveyJS Survey   

export default {
  name: 'Test',
  components: {
    Survey // SurveyJS  
  },
  methods:{
    calculate_mbti : function(results){
      let i_or_e = 0    
      let n_or_s = 0
      let t_or_f = 0
      let j_or_p = 0

      console.log(i_or_e, n_or_s, t_or_f, j_or_p)
      
      for (let i = 1; i< 13; i++){
        let tmp = results[i]
        
        switch (tmp){
          case "i": 
            i_or_e += 1;
            break;
          case "e": 
            i_or_e --;
            break;
          case "n":
            n_or_s ++
            break
          case "s":
            n_or_s --
            break
          case "t":
            t_or_f ++
            break
          case "f":
            t_or_f --
            break
          case "j":
            j_or_p ++
            break
          case "p":
            j_or_p --
            break
          default:
            console.log(`#{tmp} mbti   .`)
          }
        }
        
      console.log(i_or_e, n_or_s, t_or_f, j_or_p)
      let mbti = ""
      if (i_or_e > 0){
        mbti = mbti + "i"
      }
      else{
        mbti = mbti + "e"
      }

      if (n_or_s > 0){
        mbti = mbti + "n"
      }
      else{
        mbti = mbti + "s"
      }

      if (t_or_f > 0){
        mbti = mbti + "t"
      }
      else{
        mbti = mbti + "f"
      }

      if (j_or_p > 0){
        mbti = mbti + "j"
      }
      else{
        mbti = mbti + "p"
      }
      console.log(mbti)      
    }
  },
  computed: {
    surveyModel () { 
      let surveyModel = new SurveyVue.Model(surveyJSON) //  JSON  model 
      surveyModel.onComplete.add(function (result) { // Complete      
        // alert(`result: ${JSON.stringify(result.data)}`)
        const results = JSON.stringify(result.data)
        console.log(results) 
        this.calculate_mbti(results)
      })
      return surveyModel
    }
  }
}
</script>

